html {
  height: 100%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
*,
*:before,
*:after {
  -webkit-box-sizing: inherit;
          box-sizing: inherit;
}
body {
  position: relative;
  margin: 0;
  min-height: 100%;
  background-image: url('./assets/img/bg.jpg');
  background-position: center center;
  background-repeat:  no-repeat;
  background-attachment: fixed;
  background-size:  cover;
  max-width: 100%;
  overflow-x: hidden;
}
@font-face {
  font-family: 'Helvetica Neue';
  src: url('./assets/fonts/HelveticaNeue.ttf');
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: 'Segoe UI';
  src: url('./assets/fonts/segoeui.ttf');
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: 'Segoe UI Bold';
  src: url('./assets/fonts/segoeuib.ttf');
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: 'Segoe UI Italic';
  src: url('./assets/fonts/segoeuii.ttf');
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: 'Segoe UI Light';
  src: url('./assets/fonts/segoeuil.ttf');
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: 'Segoe UI SemiBold';
  src: url('./assets/fonts/segoeuisb.ttf');
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: 'Segoe UI SemiLight';
  src: url('./assets/fonts/segoeuisl.ttf');
  font-weight: normal;
  font-style: normal;
}
* {
  margin:0;
  padding:0;
}
textarea, input, button, fieldset { outline: none; border: none; }
.toast-text {
  font-family: 'Helvetica Neue'
}
.navbar {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: justify;
      justify-content: space-between;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 80px;
  padding: 15px 20px;
}
.nav-right {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-align: center;
      align-items: center;
}
.logo-img {
  height: 50px;
}
.mail-img {
  height: 25px;
  margin-right: 20px;
}
.appstore-img {
  height: 50px;
}
@media only screen and (max-width: 768px) {
  .navbar {
    padding: 10px 15px;
    height: 55px;
  }
  .logo-img {
    height: 35px;
  }
  .mail-img {
    height: 20px;
    margin-right: 15px;
  }
  .appstore-img {
    height: 35px;
  }
}



